import axios from '@/utils/uniAxios.js'
export default {
	data() {
		return {
			oldMobile: '',
			code: '获取验证码',
			disabled: false,
			msgCode: '',//短信验证码
			num: 60,
			id:'',
			phone:''
		}
	},
	onLoad(option) {
		this.id = option.id;
		axios.post('/sso/user/id',{id:this.id}).then(res=>{
			console.log(res)
			 this.phone = res.data.data.phone;
			this.oldMobile = this.phone.substring(0,3)+'****'+this.phone.substring(7);
		})
	},
	methods: {
		// 下一步input
		next() {
			if (this.msgCode == '') {
				Toast.fail('请输入验证码');
			} else {
				let obj ={
					authCode:this.msgCode ,
					phone: this.phone
				}
				axios.post('/sso/user/validCode',obj).then(res=>{
					if(res.data.code==200){
						uni.navigateTo({
							url: '../set/bindmobile?oldMobile='+this.oldMobile+'?id='+this.id,
						});
					}
				})
				
			}

		},
		// 获取验证码
		getCode() {
			let obj = {
				telephone:this.phone
			}
			axios.post('/sso/user/sendCode',obj).then(res=>{
				if(res.data.code=='200'){
					this.$api.msg('发送成功')
					if (this.code == '获取验证码' || this.code == '重新发送') {
						this.disabled = true;
						this.code = this.num + 's';
						var timer = setInterval(() => {
							this.num--;
							this.code = this.num + 's';
							if (this.num == 0) {
								clearInterval(timer);
								this.code = '重新发送';
								this.num = 60;
								this.disabled = false;
							}
						}, 1000)
					}
				}
			})
			
		}
	},
}
